:root {
  --color-one: #ffbe0b;
  --color-two: #fb5607;
  --color-three: #ff006e;
  --color-four: #8338ec;
  --color-five: #3a86ff;
}

html {
  background-color: #282c34;
  background-image: linear-gradient(128deg, #561400, #1d053e);
  background-attachment: fixed;
  min-height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  color: white;
  background: transparent;
}

.ui.grid {
  margin: 0;
}

button {
  padding: 1rem;
  font-size: 1.2rem;
  cursor: pointer;
  border: none;
}
